@import '../core/_core.scss';

.fill {
  padding: 27px 30px 0 40px;
  font-family: $font-text;
  text-transform: uppercase;
  color: $smaller-text;

  &__form {
    display: flex;
    flex-direction: column;
    align-items: center;
    border-bottom: $border-lines solid 1px;
    color: $main-text;

    &--label {
      margin-top: -10px;
      margin-bottom: 7px;
    }

    &--input {
      height: 30px;
      border: solid 1px $border;
      padding-left: 3px;
      margin-bottom: 30px;

      @include tablet {
        width: 100%;
      }
    }

    ::placeholder {
      font-size: 14px;
      color: $placeholder;
      font-family: $font-text;
    }

    :focus {
      outline: 0 none;
      border: 3px solid #a2deaf;
    }

    &--file {
      display: flex;
      margin-bottom: 30px;

      &--button {
        width: 140px;
        height: 30px;
        color: $color-bg-2;
        background: $icons;
        border-radius: 3px;
        border: none;
        font-size: 14px;
        text-align: center;
        padding-top: 3px;
        cursor: pointer;
        &:hover {
          background: $button-hover;
        }
      }

      &--input {
        display: none;
      }
    }
  }
}
