@import '../core/_core.scss';

.share {
  padding: 27px 20px 0;
  font-family: $font-text;
  text-transform: uppercase;
  color: $smaller-text;
  @include tablet {
    padding: 27px 30px 0 40px;
  }
  @include desktop {
  }

  &__create {
    display: flex;
    border-bottom: $border-lines solid 1px;
    justify-content: center;

    &--button {
      width: 199px;
      height: 50px;
      border-radius: 12px;
      background: $icons;
      color: $color-bg-2;
      font-size: 18px;
      font-weight: 600;
      text-transform: uppercase;
      margin-bottom: 30px;
      cursor: pointer;
      &:hover {
        background: $button-hover;
        transform: scale(1.01);
      }
    }
  }
}
